.PopupPage {
  position: fixed;
  top: 64px;
  left: 0;
  width: 100%;
  height: calc(100% - 64px);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  background: -webkit-gradient(linear, left top, left bottom, from(var(--mdc-theme-secondary-light)), to(var(--mdc-theme-primary-light)));
  background: -webkit-linear-gradient(var(--mdc-theme-secondary-light), var(--mdc-theme-primary-light));
  background: -o-linear-gradient(var(--mdc-theme-secondary-light), var(--mdc-theme-primary-light));
  background: linear-gradient(var(--mdc-theme-secondary-light), var(--mdc-theme-primary-light));
}

.PopupPage--open {
  opacity: 1;
  visibility: visible;
}

.PopupPage__inner {
  max-width: 40rem;
  /*max-height: 100%;*/
  margin: 0 auto;
  padding: 20px;
  /*overflow-y: auto*/
}



